.menu-container {
  width: 230px;
  font: 14px/1.5 Nunito, "Helvetica Neue", sans-serif;
  flex-direction: column;
  margin-bottom: 0px;
  height: 570px;
  -webkit-font-smoothing: subpixel-antialiased;
}

@media(min-width: 768px){
  .menu-container{
    width: 250px;
    margin: -17px 45px 20px 30px ;
    position: sticky;
    position: -webkit-sticky;
  }
}

.menu-container h1 {
  font-size: 28px;
  font-weight: 600;
  color: #333;
  margin-top: 7px;
}

.menu-container span {
  margin-bottom: 30px;
  margin-top: 20px;
  font-size: 14px;
  color: #727272;
  font-weight: 500;
}

.menu-container p {
  margin-top: 20px;
  margin-bottom: 30px;
    color: #727272;
  font-weight: 500;
}

.menu-container .links {
  display: flex;
  flex-direction: column;
  font-weight: 500;
  margin-top: 30px;
  margin-bottom: 30px;
}

.menu-container .links a {
  color: #267cb9;
  font-size: 14px;
  text-decoration: none;
  font-weight: 500;
  line-height: 1.2em;
  margin: 0 0 7px;
}

.menu-container div a {
  color: #267cb9;
  font-size: 14px;
  text-decoration: none;
  font-weight: 500;
  line-height: 1.2em;
  margin: 0 0 7px;
}

.menu-container .link-imds a {
  color: #f58220;
  font-size: 14px;
  text-decoration: none;
  font-weight: 600;
  line-height: 1.25em;
  margin-bottom: 10px;
}

.menu-container .links a:hover {
  color: #1e6394;
}

.menu-container img {
  width: 100%;
  margin-bottom: 20px;
}
