
#nice-figures-tables .link {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 13px;
  font-weight: 500;
  text-decoration: none;
  color: #267cb9;

  }

  #nice-figures-tables .area {
    font-weight: 600;
    font-size: 15px;
    color: #393939;
    text-decoration: none;
    margin-top: 1.5em; 
    margin-bottom: 0.5em;
    }

  #nice-figures-tables .linkexp {
    margin-top: 10px;
    margin-bottom: 20px;
    font-size: 14px;
    font-weight: 400;
    text-decoration: none;
    color: #00a99d;
    }

    #nice-figures-tables .linkdesc {
      font-size: 13px;
      font-weight: 500;
      text-decoration: none;
      color: #417be7;     
    }

    #nice-figures-tables span p a {
      font-weight: 600;
      font-size: 14px;
      color: #f58220;
      text-decoration: none;
      cursor: pointer; 
    }

  #nice-figures-tables p {
    font-weight: 900;
    font-size: 14px;
    color: #727272;
    text-decoration: none;
    margin-top: 1.5em; 
    margin-bottom: 0.25em; 

  }

  #nice-figures-tables span p {
    font-weight: 400;
    font-size: 14px;
    color: #727272;
    text-decoration: none;
    font-style: inherit;
    margin-top: 0.5em; 
    margin-bottom: .15em;

  }

  #nice-figures-tables p img {
    width: 75%;
    margin-top:10px; 
    margin-bottom:10px; 

   }
   
   #nice-figures-tables .ico {
    width: 60%;
    margin-top:10px; 
    margin-bottom:10px; 

   }

      
   #nice-figures-tables .icos {
    width: 30%;
    margin-top:10px; 
    margin-bottom:10px; 

   }
  
  