#policy-work span {
    font-weight: 600;
    font-size: 16px;
    color: #393939;
    text-decoration: none;
    cursor: pointer; 
  }

  #policy-work div{
    margin-top: 1em; 
    margin-bottom: 1.5em;
  }
  
  #policy-work p {
    font-weight: 600;
    font-size: 14px;
    color: #727272;
    text-decoration: none;
    margin-top: 1em; 
    margin-bottom: 0.5em;
  }
  
  #policy-work p a {
    font-weight: 400;
    font-size: 15px;
    color: #267cb9;
    text-decoration: none;
    cursor: pointer; 
  }